:root {
    --color-negro: #000;
    --color-blanco: rgba(255, 255, 255, 0.9);
    --color-azul: #00b5e2;
    --color-azul-oscuro: #0012b1;
}

*,
*::after,
*:before {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.contenedor {
    /* background-color: var(--color-blanco); */
    height: 100%;
    width: 100%;
    position: fixed;
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    z-index: 10000;
}

.loader-container {
    position: absolute;
    width: 300px;
    height: 300px;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.loader-container .loader {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    border: solid 4px transparent;
    border-top-color: var(--color-azul-oscuro);
    border-left-color: var(--color-azul-oscuro);
    border-radius: 50%;
    animation: loader 1.4s linear infinite;
}

.loader-container .loader2 {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    height: 70%;
    border: solid 4px transparent;
    border-top-color: var(--color-azul);
    border-left-color: var(--color-azul);
    border-radius: 50%;
    animation: loader2 1.2s linear infinite;
}

.loader-container .img-load {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50%;
    height: 50%;
    animation: loader 1.8s step-end infinite;
}

@keyframes loader {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

@keyframes loader2 {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(-360deg);
    }
}
