:root {
    --color-azul: rgb(0, 0, 0);
    --color-blanco: rgb(255, 255, 255);
    --color-azul-cian: rgb(28, 88, 255);
    --letras-btn: #fff;
}

.btn-primary {
    background-color: #073379;
    color: var(--color-blanco);
    border-color: #073379;
    border-radius: 25px;
}

